import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import PropTypes from 'prop-types';
const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 560,
    minWidth: 360,
    backgroundColor: theme.palette.background.paper,
    position: 'relative',
    overflow: 'auto',
    maxHeight: 400,
    minHeight: 300,
  },
  listSection: {
    backgroundColor: 'inherit',
  },
  ul: {
    backgroundColor: 'inherit',
    padding: 0,
  },
}));

function PinnedSubheaderList(props) {
  const classes = useStyles();
  const { defectedAccounts } = props

  PinnedSubheaderList.propTypes = {
    defectedAccounts: PropTypes.array.isRequired
  }
  return (
    <List className={classes.root} subheader={<li />}>
        {defectedAccounts.map(({account, reason}, index) => {
            return(
            <ListItem key={index}>
                <ListItemText primary={`${index+1}. ${account.url}`} secondary={`${reason}`} />
            </ListItem>
            )
        })}
    </List>
  );
  
  
}


export default PinnedSubheaderList